import React from "react";
import { Link } from "gatsby";
import { Card } from "react-bootstrap";
import { StaticImage } from "gatsby-plugin-image";

const BlogsData = (data) => {
  const { data: propsData } = data;
  propsData.edges.sort((a, b) => b.node.keyId - a.node.keyId);

  return (
    <section className="section-develop" id="ourteam">
      <div className="container grid"></div>
      <div className="container">
        <div className="row pb-5 mb-5">
          {propsData.edges?.map((ele) => {
            const { node } = ele;

            return (
              <div className="col-md-4 col-sm-6 ">
                <Card
                  key={ele.id}
                  className="developer card border rounded shadow mb-4"
                >
                  <Link to={`${node.slug}`} className="text-decoration-none">
                    {/* once image add in src/images/img-src after add to src. for time being and check the below the condition */}
                    {node?.slug === "unraveling-defi" && (
                      <StaticImage
                        src={"../../images/blog-1.png"}
                        alt="Unraveling Defi explained in image"
                      />
                    )}
                    {node?.slug === "crypto" && (
                      <StaticImage
                        src={"../../images/blog-2.png"}
                        alt="AI Crypto Trading Bot explained in image"
                      />
                    )}
                    {node?.slug === "blockchain" && (
                      <StaticImage
                        src={"../../images/blog-3.png"}
                        alt="Blockchain in supply chaing explained in image"
                      />
                    )}
                    {node?.slug === "e-commerce-in-automotive" && (
                      <StaticImage
                        src={"../../images/blog-4.png"}
                        alt="E-commerce in automotive explained in image"
                      />
                    )}
                    {node?.slug === "pharma-in-blockchain" && (
                      <StaticImage
                        src={"../../images/blog-5.png"}
                        alt="Pharma in blockchain development explained in image"
                      />
                    )}
                    {node?.slug === "software-products" && (
                      <StaticImage
                        src={"../../images/blog-6.png"}
                        alt="Software products development explained in image"
                      />
                    )}
                    {node?.slug === "crypto-trading-bot" && (
                      <StaticImage
                        src={"../../images/blog-7.png"}
                        alt="Crypto trading free bots explained in image"
                      />
                    )}
                    {node?.slug === "web-arena-with-the-graph" && (
                      <StaticImage
                        src={"../../images/blog-8.png"}
                        alt="Web3 arena graphs explained in image"
                      />
                    )}
                    {node?.slug === "video-conferencing-with-bigbluebutton" && (
                      <StaticImage
                        src={"../../images/blog-9.png"}
                        alt="Video conferencing with bilbluebutton explained in image"
                      />
                    )}
                  </Link>
                  <Card.Body>
                    <div className="developer-inner">
                      <div className="d-flex justify-content-between align-items-center">
                        <Link
                          to={`${node.slug}`}
                          className="text-decoration-none text-muted"
                        >
                          <h1
                            style={{
                              fontSize: "21px",
                              fontWeight: "600",
                              overflow: "hidden",
                              textOverflow: "ellipsis",
                              display: "-webkit-box",
                              WebkitLineClamp: 2,
                              WebkitBoxOrient: "vertical",
                            }}
                            className="mb-4 text-dark fs-semibold pt-2 lh-sm"
                          >
                            {node.title}
                          </h1>
                        </Link>
                      </div>
                      <p
                        style={{
                          overflow: "hidden",
                          textOverflow: "ellipsis",
                          display: "-webkit-box",
                          WebkitLineClamp: 2,
                          WebkitBoxOrient: "vertical",
                        }}
                        className="cta-text mb-2 pt-2"
                      >
                        {node?.short_description}
                      </p>
                      <Link className="cta-text" to={`${node.slug}`}>
                        <p>Read More</p>
                      </Link>
                      <div className="d-flex align-items-center justify-content-between pt-3">
                        <p className="fs-4">{node.blog_created_date}</p>
                        <p>Site Admin</p>
                      </div>
                    </div>
                  </Card.Body>
                </Card>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};

export default BlogsData;
