import Layout from "../components/layout";
import Seo from "../components/seo";
import React from "react";
import BlogsData from "../screens/blogs";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import BreadCrumb from "../components/common/breadcrumb";

const BlogsScreen = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Blogs", link: "" },
  ];

  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <BlogsData data={props?.data?.allDataJson} />
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="Blogs"
    description="Dive into the world of innovation with JustTry Technologies' blog. Uncover the latest tech trends, gain insights from industry experts, and empower yourself with cutting-edge knowledge. Explore a fusion of expertise and creativity as we unravel the future of technology together. JustTry Technologies - where curiosity meets innovation."
    keywords="Blog, Keywords, Top 10, Tips, How-to, Guide, Insights, Trends, Best Practices, Expert advice"
  ></Seo>
);
export default BlogsScreen;

export const query = graphql`
  query MyQuery {
    allDataJson {
      edges {
        node {
          id
          keyId
          slug
          author_name
          blog_created_date
          title
          short_description
          description
          meta_title
          meta_description
          image
          category
          tags
        }
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
